.profileInfo {
    display: flex;
    background: #edeef0;
    padding-bottom: 10px;
}
.avatar {
    width: 330px;
    height: 190px;
    background: #fff;
    padding: 15px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.profileInner {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    margin-left: 15px;
    background: #fff;
    padding: 10px;
    width: 100%;
}
.profileFooter {
    display: flex;
    justify-content: space-evenly;
    flex-flow: column;
    &__postwrite {
        textarea {
            box-sizing: border-box;
            resize: none;
            width: 100%;
            height: 60px;
            padding: 10px;
        }
    }
    button {
        margin: 0 auto 20px;
    }
}
.profileDescr {
    text-align: center;
    margin-bottom: 40px;
    position: relative;
}
.profileItem {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.aboutItem {
    width: 205px;
    cursor: pointer;
}
.aboutDescr {
    margin: 0 auto;
}
.editButton {
    position: absolute;
    right: 0;
    top: 0;
    color: #999;
    cursor: pointer;
}
.profileHeader {
    &__name {
        font-weight: 600;
    }
    &__status {
        margin-top: 15px;
    }
}